html, body, #root {
  margin: 0px !important;
  padding: 0px !important;
  height: 100%;
}

:root {
  --header-height-old: 34px;
  --header-height: 0px;
  --footer-height: 60px;
}

.app_container {
  padding: 0px !important;
  margin: 0px !important;
  height: 100%;
}

.boxWrapper {
  width: 100vw;
  height: 100%;
}


/* START : Helper Styles */
.flexStart {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
}

.flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

.flexStartContentCenter {
  display: flex;
  justify-content: flex-start;
  align-items: center;
}

.flexEndContentCenter {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.overflowY{
  overflow-y: auto;
}
/* END : Helper Styles */

/* START : Header Styles */
.header {
  width: 100vw;
  height: 0px;
  height: var(--header-height);
  background-color: aquamarine;
  position: relative;
}

.menu {
  position: absolute;
  right: 0px;
  top: 0px;
  z-index: 999;
}

.checkedVegCounts > table {
  width: 80%;
  /* border-collapse: 'collapse'; */
}

.checkedVegCounts > table,
.checkedVegCounts > table th,
.checkedVegCounts > table td {
  border: 1px solid;
}

.checkedVegCounts > table th,
.checkedVegCounts > table td {
  padding: 10px;
}
/* END : Header Styles */


/* START : Body Styles */
.body {
  width: 100vw;
  height: calc(100% - 0px - 60px);
  height: calc(100% - var(--header-height) - var(--footer-height));
  background-color: bisque;
  overflow-x: auto;
}

.rupees-cell:before,
.rupees-total-cell:before {
  content: "Rs.";
}

.measure-cell,
.rupees-cell,
.rupees-total-cell {
  position: relative;
}

.measure-cell .MuiCheckbox-root,
.rupees-total-cell .MuiCheckbox-root,
.rupees-cell .MuiCheckbox-root {
  position: absolute;
  width: 100%;
  height: 100%;
  padding: 0px;
  margin: 0px;
  z-index: 999;
  left: 0px;
  right: 0px;
  top: 0px;
  opacity: 0.3;
}

.measure-cell .MuiCheckbox-root.Mui-checked,
.rupees-total-cell .MuiCheckbox-root.Mui-checked,
.rupees-cell .MuiCheckbox-root.Mui-checked {
  opacity: 1;
  background: #d2194447;
  border-radius: 0;
}

.measure-cell .MuiCheckbox-root svg,
.rupees-total-cell .MuiCheckbox-root svg,
.rupees-cell .MuiCheckbox-root svg {
  opacity: 0;
}

.measure-cell .MuiCheckbox-root svg,
.rupees-total-cell .MuiCheckbox-root input,
.rupees-cell .MuiCheckbox-root input {
  width: 100%;
}

.actionIcons {
  position: relative;
}

.overLayDisable {
  /* width: calc(100vw - 24px - 3px); */
  width: 100%;
  position: absolute;
  height: 20px;
  left: 0px;
  z-index: 999;
  background: rgba(255,255,255,0.1);
}
/* END : Body Styles */

/* START : Footer Styles */
.footer {
  width: 100vw;
  height: 60px;
  height: var(--footer-height);
  background-color: #c2c2c2;
  overflow: hidden;
  padding: 0px 10px;
}

.vegStackAlphaFilter.fullWidth {
  width: 100vw;
}

.vegStackAlphaFilter {
  position: absolute;
  bottom: 50px;
  z-index: 999;
  width: 90vw;
}

.rangerCheckbox {
  position: absolute;
  bottom: 40px;
  z-index: 999;
  right: 0px;
}

.inputRange,
.inputRange input {
  width: 100%;
}
/* END : Footer Styles */
